@import url(https://fonts.googleapis.com/css?family=Ruthie&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html {
  height: 100%;
  width: 100%; }

#root {
  height: 100%;
  width: 100%; }

body {
  height: 100%;
  width: 100%;
  position: relative;
  background-color: black;
  color: white; }

.App {
  height: 100%;
  width: 100%;
  text-align: center; }

.Quotes {
  height: 100%;
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  font-size: calc(30px + 3vw);
  font-family: 'Ruthie', cursive;
  cursor: pointer;
  word-spacing: 10px; }

.Footer {
  position: absolute;
  right: 0;
  bottom: 0;
  margin: 20px;
  padding: 10px 20px;
  font-size: 0.8em;
  text-align: right;
  text-transform: uppercase;
  color: gray; }

.Footer .Title {
  font-size: 1em; }

.Footer a {
  color: inherit;
  text-decoration: none; }

#activeQuote {
  transition: opacity 1s;
  -webkit-transition: opacity 1s;
  box-sizing: border-box;
  padding: 10px 30px; }

.hidden {
  opacity: 0; }

